
.fb {
    // 공통 
    &__button {
        width: 100px;
        height: 50px;
        background: #000;
        border: 2px solid #000;
        color: #fff;
        font-size: 18px;
        vertical-align: middle;
    }

    &__todoList {
        position: relative;
        width: 100vw;
        height: 100vh;
        font-family: 'Noto-Sans';
        background: #ddd;

        .fb {
            &__todo {
                position: absolute;
                top: 50%;
                left: 50%;
                width: 750px;
                height: 1000px;
                padding: 50px;
                background: #fff;
                transform: translate(-50%, -50%);
                
                &__header {
                    position: relative;
                    width: 100%;
                    height: 185px;
                    border-bottom: 2px solid #000;
                }
                
                &__title {
                    padding-right: 300px; 
                    font-size: 40px;
                    line-height: 1;
                }
        
                &__today {
                    position: absolute;
                    top: 8px;
                    right: 0;
                    font-size: 28px;
                    line-height: 1;
                }
        
                &__addlist {
                    margin-top: 44px;
                    font-size: 0;
                    line-height: 0;
        
                    input {
                        width: 550px;
                        height: 50px;
                        border: 2px solid #000;
                        font-size: 18px;
                        vertical-align: middle;
                    }
                }
        
                &__count {
                    margin-top: 18px;
                    font-size: 0;
                }
        
                .count {
                    display: inline-block;
                    margin-right: 22px;
                    font-size: 0;
        
        
                    &:last-child {
                        margin-right: 0;
                    }
        
                    &__name {
                        margin-right: 5px;
                        font-size: 18px;
                        font-weight: bold;
                        line-height: 1;
                    }
        
                    &__value {
                        font-size: 18px;
                        line-height: 1;
                        color: #999999;
                    }
                }
        
                &__content {
                    display: none;
                    margin-top: 20px;
        
                    &.show {
                        display: block;
                    }
        
                    &--null {
                        display: none;
                        
                        &.show {
                            display: block;
                        }
        
                        .fb__todo__text {
                            padding-top: 50px;
                            text-align: center;
                        }
                    }
                }
        
                &__scroll {
                    overflow-y: auto;
                    max-height: 710px;
                }
                
                &__list {
                    position: relative;
                    padding: 20px 0;
                    border-bottom: 1px solid #aaa;
        
                    &:after {
                        display: block;
                        clear: both;
                        content: "";
                    }
        
                    &:first-child {
                        padding-top: 0;
        
                        .fb__todo__update {
                            padding-top: 0;
                        }
                    }
        
                    &:last-child {
                        border-bottom: none;
                    }
        
                    &.checked {
                        opacity: 0.4;
        
                        .fb__todo__text {
                            text-decoration: line-through;
                        }
                    }
                }
        
                &__textWrap {
                    float: left;
                    width: 564px;
                }
        
                &__text {
                    min-height: 40px;
                    font-size: 18px;
                    line-height: 20px;
                    cursor: pointer;
                }
        
                &__date {
                    display: block;
                    margin-top: 9px;
                    color: #999;
                    font-size: 14px;
                    line-height: 1;
                }
        
                &__controller {
                    float: right;
                    
                    button {
                        display: block;
                        width: 50px;
                        height: 30px;
                    }
        
                    &--rewrite {
                        background: #666;
                        margin-bottom: 4px;
                        color: #fff;
                        font-size: 14px;
                    }
        
                    &--delete {
                        background: #000;
                        color: #fff;
                        font-size: 14px;
                    }
                }
        
                &__update {
                    display: none;
                    position: absolute;
                    top: 0;
                    left: 0;
                    width: 100%;
                    height: calc(100%  - 1px);
                    padding-top: 23px;
                    background: #fff;
                    font-size: 0;
                    vertical-align: middle;
        
                    &.show {
                        display: block;
                    }
        
                    input {
                        width: calc(100% - 200px);
                        height: 50px;
                        border: 2px solid #000;
                        vertical-align: middle;
                    }
        
                    button:last-child {
                        background: #fff;
                        color: #000;
                    } 
                }
        
                //피드백반영
                &__feedback {
                    .fb__todo {
                        &__count {
                            .count {
                                &__box {
                                    display: inline-block;
                                    margin-right: 22px;
                                }
        
                                &__title {
                                    margin-right: 5px;
                                    font-size: 18px;
                                    font-weight: bold;
                                    line-height: 1;
                                }
        
                                &__value {
                                    font-size: 18px;
                                    line-height: 1;
                                    color: #999999;
                                }
                            }
                        }
        
                        &__content {
                            display: block;
                        }
                
                        &__scroll {
                            overflow-y: auto;
                            max-height: 710px;
                            margin-top: 20px;
                        }
                        
                        &__list {
                            &.done {
                                opacity: 0.3;
                
                                .fb__todo__text {
                                    text-decoration: line-through;
                                }
                            }
                        }
               
                        &__inner {
                            float: left;
                            width: 564px;
                        }
        
                        &__text {
                            cursor: pointer;
                        }
                
                        &__empty {
                            display: none;
                            padding: 100px 0;
                            font-size: 20px;
                            text-align: center;
                
                            &.show {
                                display: block;
                            }
                        }
                    }
                
                }
            }
        }
    }
    
}